.title {
  color: #E4E3D9;
  text-align: center;
  font-family: 'Audiowide' ;
  font-size: 2.5em;

}

.game-area {
  position: relative;
  margin: 50px auto;
  width: 500px;
  height: 500px;
  border: 1px solid #000000;
  background-color: #FAF7FF;
}

.snake-dot {
  position: absolute;
  width: 2%;
  height: 2%;
  background-color: #000000;
  border: 1px solid #FAF7FF;
  z-index: 2;
}

.snakeLenght {
  text-align: center;
color: #E4E3D9;
font-family: 'Raleway', sans-serif;
font-weight: 300;
}

.snake-food {
  position: absolute;
  width: 2%;
  height: 2%;
  background-color: #A30000;
  border: 1px solid #FAF7FF;
  z-index: 1;
}

.modal {
  display: none;
    position: fixed;
    z-index: 10;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
}

.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
}

.close {
  color: #aaaaaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

body {
  background-color: #00693C !important;
}
